






import { Component, Vue } from 'vue-property-decorator';

@Component
export default class User extends Vue {
    error = "";

    logout() {
        this.axios.delete("/api/v1/session", {withCredentials: true}).then(()=>{
            this.$router.push("/")
        }).catch((reason)=>{
            this.error = reason.response.data;
        })
    }
}
