





















import { Component, Vue } from 'vue-property-decorator';

@Component
export default class User extends Vue {
    query = {
        user: {
            username : "",
            password : "",
            password_confirmation : ""
        }
    };
    error = {};

    login() {
        this.axios.post("/api/v1/users.json", this.query, {withCredentials: true}).then((response)=>{
            this.axios.post("/api/v1/session", this.query.user, {withCredentials: true}).then(()=>{
                this.$router.push("/")
            })
        }).catch((reason)=>{
            this.error = reason.response.data;
        })
    }
}
