















import { Component, Vue } from 'vue-property-decorator';

@Component
export default class User extends Vue {
    query = {
        username : "",
        password : ""
    };
    error = {};

    login() {
        this.axios.post("/api/v1/session", this.query, {withCredentials: true}).then(()=>{
            this.$router.push("/")
        }).catch((reason)=>{
            this.error = reason.response.data;
        })
    }
}
